const routes = require('./routes.json')
const summary = require('./summary')
export default {
  mounted () {
    this.height = window.screen.availHeight + 'px'
    this.columnStyle = {
      height: this.height,
      overflow: 'scroll',
      position: 'fixed',
      top: 0
    }
  },
  data () {
    return {
      summary,
      days: Math.ceil((new Date().getTime() - new Date('2016/02/14').getTime()) / (24 * 3600 * 1000)),
      height: '1000px',
      columnStyle: {
        height: '1000px'
      },
      routes,
      currentCategory: this.$route.path.split('/')[2]
    }
  },
  head: {
    title: 'VUX'
  }
}



